@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.uploader {
  display: block;
  clear: both;
  margin: 0 auto;
  width: 100%;
  max-width: 600px;
}

label {
  float: left;
  clear: both;
  width: 100%;
  padding: 2rem 1.5rem;
  text-align: center;
  background: #fff;
  border-radius: 7px;
  border: 3px solid #eee;
  transition: all 0.2s ease;
  -webkit-user-select: none;
      -ms-user-select: none;
          user-select: none;
}

#start {
  float: left;
  clear: both;
  width: 100%;
}

.icon {
  transform: scale(0.8);
  opacity: 0.3;
}

input[type="file"] {
  display: none;
}

div {
  margin: 0 0 0.5rem 0;
}

.btn {
  display: inline-block;
  margin: 0.5rem 0.5rem 1rem 0.5rem;
  clear: both;
  font-weight: 700;
  font-size: 20px;
  text-decoration: none;
  text-transform: none;
  text-transform: initial;
  border: none;
  border-radius: 0.2rem;
  outline: none;
  padding: 0 1rem;
  height: 36px;
  line-height: 36px;
  color: #1ee2e7;
  box-sizing: border-box;
  background: #000000;
  border-color: #000000;
  cursor: pointer;
  font-family: "Alata";
}

.btn:hover {
  color: #fec503;
}

#outer {
  width: 100%;
  text-align: center;
}

.inner {
  display: inline-block;
}

#file-label:hover {
  border-color: #000000;
}

.responsive {
  width: 100%;
  height: auto;
}

.upload-div {
  margin-top: 40px;
}

.uploader-title {
  color: #000000;
}

.loader-div {
  margin-top: 25%;
}

.navbar-div {
  display: flex;
  height: 70px;
  background-color: #222;
  margin: 0;
  text-align: center;
  align-items: center;
}

.navbar-heading {
  font-family: "Alata";
  color: #1ee2e7;
  background-color: transparent;
  margin-left: 12px;
  margin-top: 0;
  margin-bottom: 0;
  font-size: 30px;
}

.navbar-heading:hover {
  color: #fec503;
}

.navbar-item {
  background-color: transparent;
}

.navbar-list {
  display: flex;
  align-items: center;
}

.result-section {
  margin-top: 25%;
  display: flex;
  align-items: center;
  justify-content: center;
}

.result-text {
  font-family: "Alata";
  font-size: 40px;
}

html,
body,
* {
  box-sizing: border-box;
  font-size: 16px;
  background-color: white;
}

h2 {
  font-family: "Alata";
  font-size: 26px;
  line-height: 1;
  margin-bottom: 0;
}

.App {
  text-align: center;
  display: block;
  width: 1360px;
  height: 100%;
  background-color: white;
  padding: 0;
}

